
// # 1 Random Words 
// # 2 Code Words
// # 3 Random Characters
export function generatePassword(type, length, dict){
    let str = "";
    switch (type){
      case 1:
        while(str.length < length){
          str += randomWord(dict);
        }
        break;
      case 2:
        for (var i = 0; i < length; i++){
          str += randomCharacter();
        }
        break;
      case 3:

        break;
      default:
        break;
    }
    return str;
  }
  
  
export function randomWord(dict){
    var keys = Object.keys(dict);
    var result = keys[keys.length * Math.random() << 0];
    console.log(result);
    return result;
}
  
export function randomCharacter(){
    var chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*()_+';
    var charLen = chars.length;
    return chars.charAt(Math.floor(Math.random() * charLen));
}



  