class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      inpass    : [],
      outpass   : [], 
      errors    : [],
      incurrent : "",
      outcurrent: "",
      select    : false
    }

    this.handleChangeLister = this.handleChangeLister.bind(this);
    this.handleClickLister = this.handleClickLister.bind(this);

    this.handleChangeGenerator = this.handleChangeGenerator.bind(this);
    this.handleClickGenerator = this.handleClickGenerator.bind(this);
  }

  componentDidMount() {
    // Get the dictionary
    axios.get("https://raw.githubusercontent.com/redbo/scrabble/master/dictionary.txt")
      .then(response => {
        //console.log(response);
        makeDict(response.data);
      })
      .catch(error => {
        console.log(error);
      })
  }

  render(){
    let inputStyle = {
      padding: "10px",
      outline: "none"
    }

    let flexStyle = {
      display: "flex"
    }

    let flexCol = {
      display: "flex",
      flexDirection: "column"
    }
    
    const CurrPass = this.state.incurrent;
    var CurrentPass;
    if(CurrPass != null && CurrPass != "" && CurrPass != undefined){
      let barhue = {
        background: CurrPass.barStyle.background
      }
      CurrentPass = (
            <InitPass strength={CurrPass.strength} barStyle={barhue} bits={CurrPass.bits}></InitPass>
          )
    } else {
      CurrentPass = (
        null
      )
    }

    const GenList = this.state.outpass.map(pass => {
      return (
          <Pass key={pass.name} name={pass.name} strength={pass.strength} cracktime={pass.cracktime} barStyle={pass.barStyle} bits={pass.bits} rank={pass.rank} issues={pass.issues}></Pass>
      )
    })

    const select = this.state.select;
    var newGenerator;
    if(select){
      newGenerator = (
        <div>
          <div style={flexStyle}>
              <input  name="outcurrent" onChange={this.handleChangeGenerator} type="text"></input>
              <button name="1"          onClick= {this.handleClickGenerator}>Generate</button>
          </div>
          <select id="genSelect" name="generators" size="3">
              <option value="1">Random Words</option>
              <option value="2">Random Characters</option>
              <option value="3">Passphrase</option>
          </select>
        </div>
      )
    } else {
        newGenerator = (
          <div>
            <Generator></Generator>
            <div style={flexCol}>
              <div>
                <button name="1" onClick= {this.handleClickGenerator}>Generate</button>
              </div>
              <select id="genSelect" name="generators" size="3">
                  <option value="1">Random Words</option>
                  <option value="2">Random Characters</option>
                  <option value="3">Passphrase</option>
              </select>
            </div>
            <table>
              <Title></Title>
              {GenList}
            </table>
          </div>
        )
    }

    const PassList = this.state.inpass.map(pass => {
      return (
          <Pass key={pass.name} name={pass.name} strength={pass.strength} cracktime={pass.cracktime} barStyle={pass.barStyle} bits={pass.bits} rank={pass.rank} issues={pass.issues}></Pass>
      )
    })

  

    return (
      <div className="App">
        <header className="App-header">
          <Header></Header>
          <h2>Compare</h2>
          <div style={flexStyle}>
            <input  id="inputPass" name="incurrent" onChange={this.handleChangeLister} type="text"></input>
            <button                name="4"         onClick={this.handleClickLister}> Add </button>
          </div>
          <div style={inputStyle}>
            {CurrentPass}
          </div>
          <table>
            <Title></Title>
            {PassList}    
          </table> 
          {newGenerator}
          <Guideline></Guideline>
        </header>
      </div>
    );
  }

  handleChangeLister(e) {
    console.log("Handling change. (Lister) ")
    var val = e.target.value;
    if(val.length > 20){
      val = val.substring(0, 20);
      e.target.value = val;
    }
    const value = analyzePassword(val);
    this.setState({
      ...this.state,
      [e.target.name]: value
    });
  }

  handleClickLister(e){
    console.log("Handling click. (Lister)");
    if(this.state.incurrent != "" && this.state.incurrent != null){
      let temp = [...this.state.inpass, this.state.incurrent];
      temp.sort(comparePass);
      let i = 1;
      temp.forEach(function (element) {
        element.rank = i;
        i++;
      });
      this.setState({
          ...this.state,
          inpass: temp,
          incurrent: null
      });
    }
    let input = document.getElementById("inputPass");
    input.value = "";
  }

  handleChangeGenerator(e){
    console.log("Handling change. (Generator) ")
  }

  handleClickGenerator(){
    console.log("Handling click. (Generator)");
    let e = document.getElementById("genSelect");
    let pass = "";
    switch(e.value){
      case "1":
        pass = generatePassword(1, 16, dict);
        break;
      case "2":
        pass = generatePassword(2, 16, dict);
        break;
      case "3":
        if(false === true){
          pass = generatePassword(3, this.state.outcurrent);
        }
        break;
      default:
        console.log("Case not found.")
        break;
    }
    let val = pass.toLowerCase();
    if(val.length > 20){
      val = val.substring(0, 20);
    }
    const value = analyzePassword(val);
    if(value != undefined){
      this.setState({
        ...this.state,
        outcurrent: "",
        outpass: [...this.state.outpass, value]
      });
    }
  }


}

// Sort passwords base on length
function comparePass(a, b){
  console.log(typeof(a.bits))
  if(Number(a.bits) < Number(b.bits)){
    return 1;
  }
  if(Number(a.bits) > Number(b.bits)){
    return -1;
  }
  return 0;
}

// Analyze Password
function analyzePassword(e){
  if(e != null){
    let text = e;
    if(checkString(text)){
      let charpool = pw_type(text);
      let length = text.length;
      let bits = entropy(length, charpool.amount);
      let comb = combinations(length, charpool.amount);
      let crack = cracktime(comb)
      crack = expo(crack, 2);
      let obj = pw_strength(bits);
      let style = addPassword(obj.value);
      bits = financial(bits);
      return {name: text, strength: obj.strength, cracktime: crack, bits:bits ,barStyle: style};
    }
  }
}

function expo(x, f) {
  return Number.parseFloat(x).toExponential(f);
}

function financial(x) {
  return Number.parseFloat(x).toFixed(2);
}

// Password Hue
function passHue(value){
  let colors = quantize(interpolateRgb("red", "green"), 5);
  return colors[value]
}

// Passbar style
function addPassword(value){
  let percentage = 20*(value+1);
  let hue = passHue(value);
  let style = {
    width: percentage + "%",
    background: hue
  }
  return style;
}

// Check if string is valid.

function checkString(string){
  if(string == null) {
    console.log("String is null.")
  } else if(string == "") {
    console.log("String is empty.")
  } else {
    return true;
  }
}

// Amount of time in seconds until crack.
function cracktime(comb){
  return comb/CPS;
}

function problems(string) {
  let problems = []
  let p1 = strNumber(string);
  //console.log(p1);
  let word = findWord(string);
}

function makeDict(txt){
  // Get an array of all the word
  var words = txt.split("\n");
  // And add them as properties to the dictionary lookup
  // This will allow for fast lookups later
  for (var i = 0; i < words.length; i++ ) {
    dict[words[i]] = true;
  }
}

function findWord(letters) {
  // Clone the array for manipulation
  var curLetters = letters.split('');
  //console.log(curLetters)
  let word = "";
  // Make sure the word is at least 3 letters long
  while (curLetters.length > 2) {
      // Get a word out of the existing letters
      word = curLetters.join("").toUpperCase();
      // And see if it's in the dictionary
      
      if (dict[word]) {
          // If it is, return that word
          console.log("FOUND: " + word);
          return word;
      }
      // Otherwise remove another letter from the end
      curLetters.pop();
  }
}

